*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.app{
  background-image: url('./assests/images/back_1.png');
  background-repeat: no-repeat;
  background-position: bottom;
  transition: 0.4 ease-out;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: 0 2px 29.12px 2.88px rgba(0,0,0,.14);
}
body {
  background: #2c3e50;
}
.app.dark{
  background-image: url('./assests/images/back_2.png');
}
.app.sunny{
  background-image: url('./assests/images/sunny.jpg');
}
.app.windy{
  background-image: url('./assests/images/windy.jpg');
}
.app.cloud{
  background-image: url('./assests/images/cloud.jpg');
}
.app.rain{
  background-image: url('./assests/images/rain.jpg');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, #0003 , #00000063);    box-shadow: 0 2px 29.12px 2.88px rgba(0,0,0,.14);
}
.search{ 
  width: 100%;
  margin: 10px 0px 20px;
  height: 40px;
  border: 1px solid #e0dfdf;
  padding: 6px;
  font-size: 17px;
  border-radius: 30px;
  box-shadow: 0 0 5px 4px #00000036;
  font-weight: bold;
  text-transform: capitalize;
}
.searchbox {
  text-align: center;
  width: 90%;
  margin: 0 auto;
  position: relative;
}
.locationbox {
  width: 90%;
  margin: 0 auto;
}
.locationname {
  font-size: 38px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  margin: 2px;
}
.datetime {
  font-size: 15px;
  color: #fff;
  text-align: center;
  font-weight: bold;
  font-style: italic;
  margin: 5px;
}.temperature div {
  background: #ffffff8f;
  padding: 30px 0px 30px 0px;
  border-radius: 15px;
  box-shadow: 0 0 6px 0px #000000b3;
  color: #fff;
  font-size: 45px;
  font-weight: bold;
  text-shadow: 0 0 2px black;
  width: 100%;
}
.temperature {
  text-align: center;
}
.weather {
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  text-transform: capitalize;
  text-align: center;
}
.temperature h2{
  font-size: 20px;
  clear: both;
  text-align: center;
}
input#searchboxinput::-webkit-input-placeholder { /* Edge */
  color: #000;
}

input#searchboxinput:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #000;
}

input#searchboxinput[type="text"]:focus{
  outline: none;
  box-shadow: 0px 0px 5px #61C5FA;
  border:1px solid #5AB0DB;
  }
  
  input#searchboxinput[type="text"]:hover{
  border: 1px solid #999;
  border-radius: 5px;
  }
  
  input#searchboxinput[type="text"]:focus:hover{
  outline: none;
  box-shadow: 0px 0px 5px #61C5FA;
  border:1px solid #5AB0DB;
  border-radius:0;
  
  }    â€‹
input#searchboxinput::placeholder {
  color: #000;
}




  .searchinitial {
    text-align: center;
    font-size: 14px;
    color: #fff;
    font-weight: bold;
    background: #3F51B5;
    width: 80%;
    margin: 0 auto;
    border-radius: 30px;
    padding: 10px;
    text-transform: capitalize;
    margin-top: 20px;    box-shadow: 0 2px 29.12px 2.88px rgba(0,0,0,.14);
}
.temperature div img {
  float: left;
  margin-top: -30px;
}
.autosuggesion {
  position: absolute;
  top: 50px;
  width: 100%;
  text-align: left;
  z-index: 9999;
  padding: 0px 5px;
}
.autosuggesion li {
  background: #fff;
  width: 100%;
  padding: 10px;
  list-style-type: decimal;
  cursor: pointer;
  font-weight: bold;
  color: #263650;
  font-size: 16px;
}

.weatherother li {
  width: 50%;
  float: left;
  padding: 5px;
  list-style-type: none;
  font-size: 17px;
  color: #fff;
  text-align: center;
  margin: 5px 0px 0px 0px;
}
.weatherother {
  border-top: 1px solid #ccc;
  margin-top: 15px;
}

/*background*/
#Clouds {
  position: absolute;
  
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 30%;
  overflow: hidden;
  -webkit-animation: FadeIn 3s ease-out;
          animation: FadeIn 3s ease-out;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
          opacity: 0.6;
}
@-webkit-keyframes FadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes FadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Cloud {
  position: absolute;
  width: 100%;
  background-repeat: no-repeat;
  background-size: auto 100%;
  height: 70px;
  -webkit-animation-duration: 120s;
          animation-duration: 120s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-name: Float, FadeFloat;
          animation-name: Float, FadeFloat;
  z-index: 1;
}
.Cloud.Foreground {
  height: 10%;
  min-height: 20px;
  z-index: 3;
}
.Cloud.Background {
  height: 9.09090909%;
  min-height: 8px;
  -webkit-animation-duration: 210s;
          animation-duration: 210s;
}
@-webkit-keyframes Float {
  from {
    -webkit-transform: translateX(100%) translateZ(0);
            transform: translateX(100%) translateZ(0);
  }
  to {
    -webkit-transform: translateX(-15%) translateZ(0);
            transform: translateX(-15%) translateZ(0);
  }
}
@keyframes Float {
  from {
    -webkit-transform: translateX(100%) translateZ(0);
            transform: translateX(100%) translateZ(0);
  }
  to {
    -webkit-transform: translateX(-15%) translateZ(0);
            transform: translateX(-15%) translateZ(0);
  }
}
/*
@keyframes Float {
  from { transform: translateX(100%) translateY(-100%) translateZ(0); }
  50% { transform: translateX(55%) translateY(0) translateZ(0); }
  to { transform: translateX(-5%) translateY(-100%) translateZ(0); }
}
*/
@-webkit-keyframes FadeFloat {
  0%,
  100% {
    opacity: 0;
  }
  5%,
  90% {
    opacity: 1;
  }
}
@keyframes FadeFloat {
  0%,
  100% {
    opacity: 0;
  }
  5%,
  90% {
    opacity: 1;
  }
}
